import { FC, Fragment, useCallback } from 'react';

import styled from 'styled-components';

import { Box } from '@openfin/ui-library';

import { getBrowserWindow } from '@common/utils/get-browser-window';
import { currentOFIdentity } from '@common/utils/window';

import { RecentSite } from '@enterprise/components/ControlBar/SearchMenu/RecentSite';
import { useRecentlyVisitedSites } from '@enterprise/components/LandingPage/useRecentlyVisitedSites';

import { NavigationRequest } from '@client-platform/shapes';

export const RecentSitesList: FC = () => {
    const recentlyVisited = useRecentlyVisitedSites();

    const handleSelectedSite = useCallback(
        async (index: number) => {
            const site = recentlyVisited[index];
            if (site) {
                const navigationRequest: NavigationRequest = {
                    action: 'result-selected',
                    windowIdentity: currentOFIdentity,
                    viewIdentity: fin.me.identity,
                    site
                };
                const browserWin = await getBrowserWindow();
                // eslint-disable-next-line no-underscore-dangle
                browserWin._handleRequestNavigation(navigationRequest);
            }
        },
        [recentlyVisited]
    );

    return (
        <RecentContainer>
            <ListContainer>
                {recentlyVisited?.map((site, index) => (
                    <Fragment key={site.id}>
                        <RecentSite
                            id={site.id}
                            site={site}
                            index={index}
                            isSelected={false} // visual state handled by focus, don't need isSelected
                            onSelectedSite={handleSelectedSite}
                        />
                    </Fragment>
                ))}
            </ListContainer>
        </RecentContainer>
    );
};

const ListContainer = styled(Box)`
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    align-self: stretch;
    min-width: 272px;

    & > a {
        display: none;
    }

    & > a:hover,
    & > a:focus-visible {
        border-radius: ${({ theme }) => theme.radius.small};
        background-color: ${({ theme }) => theme.palette.background4};

        div div:first-child {
            background-color: ${({ theme }) => theme.palette.background3};
        }
    }

    & > a:focus-visible {
        outline: solid 1px ${({ theme }) => theme.palette.statusActive};
        outline-offset: -1px;
    }

    @media (min-height: 635px) {
        display: inherit;
        & > a:nth-child(-n + 2) {
            display: inherit;
        }
    }
    @media (min-height: 675px) {
        display: inherit;
        & > a:nth-child(-n + 3) {
            display: inherit;
        }
    }
    @media (min-height: 715px) {
        display: inherit;
        & > a:nth-child(-n + 4) {
            display: inherit;
        }
    }
    @media (min-height: 755px) {
        display: inherit;
        & > a:nth-child(-n + 5) {
            display: inherit;
        }
    }
`;

const RecentContainer = styled(Box)`
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-end;
    gap: 12px;
    flex: 1 1 80%;
    width: 100%;
    padding-right: ${({ theme }) => theme.px.small};
`;
