import React, { useEffect, useState } from 'react';

export const useLocalTimeState = () => {
    const [dateString, setDateString] = useState<string>('');
    const [timeString, setTimeString] = useState<string>('');

    const updateTime = React.useCallback(() => {
        const locale = navigator.languages.length > 0 ? navigator.languages[0] : 'en-US';
        setDateString(
            new Date().toLocaleDateString(locale, {
                weekday: 'long',
                year: 'numeric',
                month: 'long',
                day: 'numeric'
            })
        );
        setTimeString(new Date().toLocaleTimeString(locale, { hour: 'numeric', minute: '2-digit' }));
    }, []);

    useEffect(() => {
        updateTime();
        const tickerInteval = setInterval(updateTime, 5000);
        return () => {
            clearInterval(tickerInteval);
        };
    }, [updateTime]);

    return { dateString, timeString };
};
