import { Fragment, useRef } from 'react';

import Head from 'next/head';

import styled from 'styled-components';

import { Box, Text } from '@openfin/ui-library';

import { enterpriseLandingPageTestIds } from '@common/utils/test-ids';

import { useTranslation } from '@common/api/i18next';

import { useBaseUrl } from '@enterprise/utils/base-url-context';

import { SiteCard } from '@enterprise/components/ControlBar/SearchMenu/SiteCard';
import BrandIcon from '@enterprise/components/LandingPage/BrandIcon';
import { LocalTime } from '@enterprise/components/LandingPage/LocalTime';
import { RecentSitesList } from '@enterprise/components/LandingPage/RecentSitesList';
import { useCuratedContent } from '@enterprise/components/LandingPage/useCuratedContent';

import { Site } from '@client-platform/shapes';
import { useScrollbarAlphaOnHover } from '@common/hooks/useScrollbarAlphaOnHover';
import useSystemSchemeCheck from '@common/hooks/useSystemSchemeCheck';

import styles from './LandingPage.module.css';

const HERE_LOGO_DIMENSION = 56;

/**
 * Landing page for new page active tab
 */
export const LandingPage = () => {
    const { browserBaseUrl } = useBaseUrl();
    const [sites, handleSelectedSite] = useCuratedContent();

    const { isSchemeDark } = useSystemSchemeCheck();
    const { t } = useTranslation();
    const favicon = `${browserBaseUrl}/enterprise/icons/default-favicon-enterprise-browser-${
        isSchemeDark ? 'dark' : 'light'
    }.svg`;
    const gridRef = useRef<HTMLDivElement>(null);
    useScrollbarAlphaOnHover(gridRef);

    return (
        <>
            <Head>
                <title>New Tab</title>
                <link rel="icon" type="image/svg" href={favicon} />
            </Head>
            <Container data-testid={enterpriseLandingPageTestIds.container}>
                <MainContainer>
                    <TopContainer>
                        <Main>
                            <LandingPageHeader as="h2">{t('appResults.header')}</LandingPageHeader>
                            {sites.length === 0 && (
                                <LandingPageSubHeader>{t('landingPage.subheader')}</LandingPageSubHeader>
                            )}
                            <GridBox
                                ref={gridRef}
                                gap="xxlarge"
                                flexBasis="64px"
                                flexGrow={0}
                                flexShrink={0}
                                data-testid={enterpriseLandingPageTestIds.siteGridContainer}
                            >
                                {sites?.map((site: Site, idx: number) => (
                                    <Fragment key={site.id}>
                                        <SiteCard
                                            site={site}
                                            index={idx}
                                            onSelectedSite={handleSelectedSite}
                                            isSelected={false}
                                            id={site.id}
                                        />
                                    </Fragment>
                                ))}
                            </GridBox>
                        </Main>
                        <LocalTime />
                    </TopContainer>
                    <LowerContainer>
                        <RecentlyVisitedSites
                            id={styles.recentlyVisitedSites}
                            data-testid={enterpriseLandingPageTestIds.recentSitesContainer}
                        >
                            <LandingPageHeader as="h2">{t('recentlyClosed.header')}</LandingPageHeader>
                            <RecentSitesList />
                        </RecentlyVisitedSites>
                        <BrandLogoContainer
                            id={styles.brandLogoContainer}
                            data-testid={enterpriseLandingPageTestIds.brandLogoContainer}
                        >
                            <BrandIcon size="large" />
                        </BrandLogoContainer>
                    </LowerContainer>
                </MainContainer>
            </Container>
        </>
    );
};

const MIN_WIDTH_SHIFT_LAYOUT = '552px';

const RecentlyVisitedSites = styled(Box)``;

const BrandLogoContainer = styled.div``;

const Container = styled(Box)`
    height: 100vh;
    overflow: hidden;
    flex-direction: column;
    padding: ${({ theme }) => theme.px.xxlarge};
    background: var(--workspace-palette-selectedTab);
    // TODO: WRK-4344 add breakpoints to theme
    @media (min-width: ${MIN_WIDTH_SHIFT_LAYOUT}) {
        padding: ${HERE_LOGO_DIMENSION}px ${HERE_LOGO_DIMENSION}px;
    }
`;

const MainContainer = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    flex: 1 0 0;
    align-self: stretch;
`;

const TopContainer = styled(Box)`
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    align-self: stretch;
    width: 100%;
    height: 100%;
`;

const Main = styled(Box)`
    display: block;
    width: 100%;
`;

const GridBox = styled(Box)`
    @property --varScrollbarAlpha {
        syntax: '<number>';
        inherits: true;
        initial-value: 0;
    }

    // Fades in/out the scrollbar on hover to avoid juddery appearance
    transition: --varScrollbarAlpha 0.2s;

    display: grid;
    min-width: 80px;
    width: calc(100vw - 62px);
    max-width: 436px;
    max-height: 312px;
    grid-template-columns: repeat(auto-fill, minmax(64px, 1fr));
    row-gap: ${({ theme }) => theme.px.large};

    /**
     * Although we only display the y scrollbar on hover with an alpha channel tweak, we need it to always be visible
     * to the DOM so that it maintains its bounding box even when opaque. This prevents a layout shift when hovering on
     * the grid.
     */
    overflow-y: visible;
    overflow-x: hidden; // no horizontal scroll

    &::-webkit-scrollbar-thumb {
        background: rgba(
            var(--workspace-palette-scrollbar-thumb-rgb),
            var(--workspace-palette-computed-scrollbar-thumb-alpha)
        );
    }

    &::-webkit-scrollbar-track {
        background: rgba(
            var(--workspace-palette-scrollbar-track-rgb),
            var(--workspace-palette-computed-scrollbar-track-alpha)
        );
    }

    padding-right: ${({ theme }) => theme.px.large};
    justify-content: center;
`;

const LandingPageHeader = styled(Box)`
    display: flex;
    flex-flow: row;
    width: auto;
    line-height: ${({ theme }) => theme.px.xxlarge};
    font-weight: ${({ theme }) => theme.fontWeight.bold};
    font-size: ${({ theme }) => theme.fontSize.xlarge};
    margin-bottom: ${({ theme }) => theme.px.large};
`;

const LandingPageSubHeader = styled(Text)`
    font-size: ${({ theme }) => theme.fontSize.base};
    color: ${({ theme }) => theme.palette.textHelp};
`;

const LowerContainer = styled(Box)`
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    align-self: stretch;
`;
