import React, { FC } from 'react';

import styled from 'styled-components';

import { Box } from '@openfin/ui-library';

import { enterpriseLandingPageTestIds } from '@common/utils/test-ids';

import TextSpan from '@enterprise/components/ControlBar/Tablist/TextSpan';
import { useLocalTimeState } from '@enterprise/components/LandingPage/useLocalTimeState';

import styles from './LandingPage.module.css';

export const LocalTime: FC = () => {
    const localTimeState = useLocalTimeState();
    return (
        <TimeContainer id={styles.timeContainer} data-testid={enterpriseLandingPageTestIds.timeContainer}>
            <DateTextSpan>{localTimeState.dateString}</DateTextSpan>
            <TimeTextSpan>{localTimeState.timeString}</TimeTextSpan>
        </TimeContainer>
    );
};

const TimeContainer = styled(Box)``;

const DateTextSpan = styled(TextSpan)`
    color: ${({ theme }) => theme.palette.textDefault};
    font-family: Inter;
    font-size: ${({ theme }) => theme.fontSize.large};
    font-style: normal;
    font-weight: 500;
    line-height: 155.023%;
`;

const TimeTextSpan = styled(TextSpan)`
    color: ${({ theme }) => theme.palette.textHelp};
    font-family: Inter;
    font-size: ${({ theme }) => theme.fontSize.base};
    font-style: normal;
    font-weight: ${({ theme }) => theme.fontWeight.normal};
    line-height: 155.023%;
    opacity: 0.85;
`;
