import { useEffect, useState } from 'react';

import { getBrowserWindow } from '@common/utils/get-browser-window';

export const useRecentlyVisitedSites = () => {
    const [recentSites, setRecentSites] = useState<Array<any>>([]);

    useEffect(() => {
        const getRecentSites = async () => {
            const browserWin = await getBrowserWindow();
            // eslint-disable-next-line no-underscore-dangle
            const recents = await browserWin._getRecentlyVisitedSites();
            setRecentSites(recents);
        };
        getRecentSites();
    }, []);

    return recentSites;
};
