import { useEffect } from 'react';

/**
 * Hook that sets computed scrollbar alpha values on hover based on theme alpha variables.
 *
 * This works by reading `--workspace-palette-scrollbar-thumb-alpha` and `--workspace-palette-scrollbar-track-alpha`
 * from the root and applying their values directly to the hovered element via inline styles.
 *
 * @param ref - A React ref pointing to the scrollable DOM element
 */
export const useScrollbarAlphaOnHover = (ref: React.RefObject<HTMLElement>) => {
    useEffect(() => {
        const el = ref.current;
        if (!el) return;

        const getAlphaVar = (name: string) => {
            const value = getComputedStyle(document.documentElement).getPropertyValue(name);
            const parsed = parseFloat(value.trim());
            return isNaN(parsed) ? 1 : parsed;
        };

        const thumbAlpha = getAlphaVar('--workspace-palette-computed-scrollbar-thumb-alpha');
        const trackAlpha = getAlphaVar('--workspace-palette-computed-scrollbar-track-alpha');
        console.log('Scrollbar thumb alpha:', thumbAlpha);
        console.log('Scrollbar track alpha:', trackAlpha);

        const handleEnter = () => {
            el.style.setProperty('--workspace-palette-computed-scrollbar-thumb-alpha', thumbAlpha.toString());
            el.style.setProperty('--workspace-palette-computed-scrollbar-track-alpha', trackAlpha.toString());
        };

        const handleLeave = () => {
            el.style.setProperty('--workspace-palette-computed-scrollbar-thumb-alpha', '0');
            el.style.setProperty('--workspace-palette-computed-scrollbar-track-alpha', '0');
        };

        el.addEventListener('mouseenter', handleEnter);
        el.addEventListener('mouseleave', handleLeave);

        // Set initial state to hidden
        handleLeave();

        return () => {
            el.removeEventListener('mouseenter', handleEnter);
            el.removeEventListener('mouseleave', handleLeave);
        };
    }, [ref]);
};
